<template>
    <div class="experience">
            <transition-group tag="ul" mode="out-in" appear>
             <li :class="[ index%2==0 ?'even':'add']" @click="sort()" v-for="(item,index) in message" :key="index">
              <i class="el-icon-caret-right"></i>-- {{item}}
             </li>
    </transition-group>
    </div>
</template>
<script>
export default {
    data(){
        return{
            flab:false,
            message:[
                "javascript",
                "Web",
                "",
                "MV*,,",
                ",",
                ","
            ]
        }

    },
  methods:{
      sort(){
          this.message.sort(()=>{
          return Math.random()-0.4
          })
      }
  }
}
</script>
<style lang="scss" scoped>
.experience{
    width: 110%;
    height: 100vh;
    background: url("../../assets/showcase.png") no-repeat;
    background-size: cover; 
    opacity: 0.6;
    ul li{
        padding: 10px 30px;
        list-style: none;
        font-weight: bold;
        font-size: 20px;
        margin: 10px 0;
        cursor: pointer;
        border-radius: 6px;
    }
    .even{
        color: red;
        background: rgba(67, 5, 238, 0.333);
    }
    .add{
        color: aqua;
        background: rgb(1, 29, 8);
    }
    .v-enter{
          opacity: 0;
          transform: translateY(90px);
    }
    
    .v-leave-active,
    .v-enter-active{
        transition: all 1s linear;
    }
    .v-move{
         transition: all 1s linear;
    }

    

}


</style>
